.skillsBlock {
    min-height: 100vh;
    background-color: gold;
}
.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}
.title {
    background-color: #bebe87;
}
.skills {
    border: 1px solid;
    background-color: #80461d;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}